import "core-js/modules/es.array.push.js";
import { ref } from 'vue';
import { reactive, toRefs } from '@vue/reactivity';
import { ElMessage } from 'element-plus';
import $ from 'jquery';
import router from '../router/index';
export default {
  setup() {
    const vue = reactive({
      drawer: true,
      visible: true,
      labelPosition: 'right'
    });

    const open2 = () => {
      ElMessage({
        message: '注册成功',
        type: 'success'
      });
    };

    const open3 = value => {
      ElMessage({
        message: value,
        type: 'warning'
      });
    };

    let account = ref('');
    let password = ref('');
    let message = ref('');

    const register = () => {
      $.ajax({
        url: "https://so.beink.cn/user/account/register/",
        type: 'post',
        data: {
          account: account.value,
          password: password.value
        },

        success(res) {
          if (res.message === "success") {
            open2();
            router.push({
              name: 'login'
            });
          } else {
            open3(res.message);
          }
        }

      });
    };

    return { ...toRefs(vue),
      account,
      password,
      message,
      register,
      open2,
      open3
    };
  }

};