import "core-js/modules/es.array.push.js";
import $ from 'jquery';
import { useStore } from 'vuex';
import router from '../router/index';
import { onMounted, reactive, toRefs } from 'vue';
import moment from 'moment/moment';
export default {
  setup() {
    const vue = reactive({
      tableData: [],
      loading: false,
      dialogVisible: false,
      index: 0
    });

    const editUser = i => {
      vue.dialogVisible = true;
      vue.index = i;
    };

    let handleDelete = a => {
      let f = confirm("确认删除" + vue.tableData[a].account);

      if (f) {
        $.ajax({
          url: 'https://so.beink.cn/user/admin/delete/',
          type: 'post',
          headers: {
            Authorization: "Bearer " + store.state.token
          },
          data: {
            id: vue.tableData[a].id
          },

          success(res) {
            for (let i in res) {
              res[i].date = moment(res[i].date).utcOffset(480).format('YYYY-MM-DD HH:mm:ss');
            }

            vue.tableData = res;
          },

          error(res) {
            console.log(res);
          }

        });
      }
    };

    let aaa = () => {
      $.ajax({
        url: 'https://so.beink.cn/user/admin/list/',
        type: 'post',
        headers: {
          Authorization: "Bearer " + store.state.token
        },
        data: {
          account: store.state.account
        },

        success(res) {
          for (let i in res) {
            res[i].date = moment(res[i].date).utcOffset(480).format('YYYY-MM-DD HH:mm:ss');
          }

          vue.loading = false;
          vue.tableData = res;
        },

        error() {}

      });
    };

    const store = useStore();
    onMounted(() => {
      const jwt = localStorage.getItem("jwt");

      if (jwt) {
        store.commit("updateToken", jwt);
        store.dispatch("getinfo", {
          success() {
            aaa();
          },

          error() {
            router.push({
              name: 'main'
            });
          }

        });
      }
    });
    return { ...toRefs(vue),
      handleDelete,
      editUser
    };
  }

};