import "core-js/modules/es.array.push.js";
import { onMounted, ref, reactive } from 'vue';
import { useStore } from 'vuex';
import router from '../../router/index';
import $ from 'jquery';
$(function () {
  $(window).scroll(function () {
    if ($(window).scrollTop() > 300) {
      $(".fas").fadeIn(50);
    } else {
      $(".fas").fadeOut(50);
    }
  });
});
export default {
  setup() {
    const vue = reactive({
      toolbar: {
        back: {
          title: '推出',
          icon: 'v-md-icon-undo',

          action() {}

        }
      }
    });
    let tabIndex = 0;
    let store = useStore();
    let title = ref('');
    const editableTabsValue = ref();
    const editableTabs = ref([]);
    const dialogVisible = ref(false);

    const addTab = () => {
      const newTabName = ++tabIndex;
      editableTabs.value.push({
        title: title.value,
        name: newTabName,
        content: `# New Tab content about ${title.value}`,
        show: true
      });
      editableTabsValue.value = newTabName;
      dialogVisible.value = false;
      title.value = '';
      save();
    };

    const removeTab = targetName => {
      let f = confirm("确定吗");
      if (!f) return;
      const tabs = editableTabs.value;
      editableTabsValue.value = targetName - 1 || 1;
      editableTabs.value = tabs.filter(tab => tab.name !== targetName);
      const Tabs = editableTabs.value;
      Tabs.forEach((i, index) => {
        i.name = index + 1;
      });
      save();
    };

    const goBack = () => {
      router.push({
        name: 'main'
      });
    };

    const save = () => {
      $.ajax({
        url: "https://so.beink.cn/user/admin/git/save/",
        data: {
          markdown: JSON.stringify(editableTabs.value)
        },
        headers: {
          Authorization: "Bearer " + store.state.token
        },
        type: 'post',

        success() {
          tabIndex = editableTabs.value.length;
        },

        error(res) {
          console.log(res);
        }

      });
    };

    const aaa = () => {
      $.ajax({
        url: "https://so.beink.cn/user/admin/git/show/",
        type: 'post',

        success(res) {
          editableTabs.value = JSON.parse(res);
          tabIndex = editableTabs.value.length;
        }

      });
    };

    onMounted(() => {
      editableTabsValue.value = Number(localStorage.getItem("activeName")) || 1;
      aaa();
      const jwt = localStorage.getItem("jwt");

      if (jwt) {
        store.commit("updateToken", jwt);
        store.dispatch("getinfo", {
          success() {
            aaa();
          },

          error() {}

        });
      }
    });

    const changeTab = () => {
      localStorage.setItem("activeName", editableTabsValue.value);
    };

    return {
      title,
      vue,
      addTab,
      removeTab,
      editableTabsValue,
      editableTabs,
      save,
      goBack,
      dialogVisible,
      changeTab
    };
  }

};